import * as React from 'react';
import * as classNames from 'classnames';
import {BaseProps, ButtonNavigation, ButtonNavigationItem, Icon, Content, Swiper, Card, CardHeader, CardContent, CardFooter
    , Button, Avatar, MessageBox} from 'bee-mobile';
import './App.scss';

interface ButtonNavigationCaseState {
    index: number;
}

export default class App extends React.PureComponent {

    state: ButtonNavigationCaseState = {
        index: 0
    };
    handleChange = (event: any) => {
        this.setState({
            index: event.index
        });
    };

    handleOpenConfirm = () => {
        MessageBox.confirm({
            title: '',
            message: ','
        });
    }

	render() {
        const {className, ...other} = this.props;
        const styleClass = classNames(
            'CardCase',
            'ButtonNavigationCase',
            className
        );
		return (
            <Content style={{transform: 'translate3d(0, 0)'}} className={styleClass} {...other}>
                <Swiper touch={false} pagination={false} activeIndex={this.state.index}>
                    <div className="box padding-10">
                        <Card>
                            <CardHeader
                                avatar={<Avatar src="assets/images/head1.jpeg"/>}
                                title=""
                                subTitle=""
                            />
                            <CardContent>
                                <Avatar src="assets/images/WechatIMG6.jpeg"/>
                                <div className="padding-10">
                                    
                                </div>
                            </CardContent>
                            <CardFooter className="padding-5">
                                <Button flat={true}>Share</Button>
                            </CardFooter>
                        </Card>
                        <Button style={{ margin: '10px' }} block onClick={this.handleOpenConfirm}></Button>
                    </div>
                    <div className="box red padding-10"></div>
                    <div className="box yellow padding-10"></div>
                </Swiper>
                <ButtonNavigation onChange={this.handleChange}>
                    <ButtonNavigationItem label="Recents" icon={<Icon icon="home"/>} />
                    <ButtonNavigationItem label="Nearby" icon={<Icon icon="accessibility"/>} />
                    <ButtonNavigationItem label="Favorites" icon={<Icon icon="extension"/>} />
                </ButtonNavigation>
            </Content>
		);
	}
}



